export class Playlist {
    constructor(
        private _id             : string ='',
        private _channelId      : string ='',
        private _title          : string ='',
        private _slug           : string ='',
        private _description    : string ='',
        private _thumbnails     : string ='',
    ){}

    public get id(){
        return this._id
    }
    public get channelId(){
        return this._channelId
    }
    public get title(){
        return this._title
    }
    public get slug(){
        return this._slug
    }
    public get description(){
        return this._description
    }
    public get thumbnails(){
        return this._thumbnails
    }

    static fromJson({id,channelId,title,slug,description,thumbnails}:Playlist) : Playlist{
        return new Playlist(id,channelId,title,slug,description,thumbnails)
    }

    static fromJsonList(array : any) : Playlist[]{
        return array.map(Playlist.fromJson)
    }
}


