import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { Ads } from '../defines/ads.class';
import { AppSetting } from '../../app.setting';

@Injectable()
export class AdsService {
    constructor(
        private _db: AngularFireDatabase
    ) { }

    getItemByPosition(position: string): Observable<Ads[]> {
        return this._db.list(AppSetting.TB_ADS, ref => ref.orderByChild('position').equalTo(position)).valueChanges().map(Ads.fromJsonList);
    };

}


