import { Component, OnInit, Input, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs/Subscription';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { Playlist } from '../../shared/defines/playlist.class';
import { Video } from '../../shared/defines/video.class';
import { VideoService } from '../../shared/services/video.service';
import { PlaylistService } from '../../shared/services/playlist.service';
import { PagerService } from '../../shared/services/pager.service';

@Component({
  selector: 'app-elm-playlist-pagination',
  templateUrl: './elm-playlist-pagination.component.html',
  styleUrls: ['./elm-playlist-pagination.component.css']
})
export class ElmPlaylistPaginationComponent implements OnInit, OnDestroy {

    @Input('playlistID')    playlistID      : string;
    @Input('layout')        layout          : string = 'grid';
    @Input('totalItems')    totalItems      : number = 5;

    playlistInfo : Playlist = null;
    videos : Video[] = null;
    pager : any = {};
    pagedItems: any[];
    subscriptionQuery : Subscription;

    constructor(
        private _videoService : VideoService,
        private _playlistService : PlaylistService,
        private _pagerService: PagerService,
        private _activatedRouteService : ActivatedRoute,
        private _routerService : Router
    ) { }

    ngOnInit() {
        this.initData();
    }
    
    ngOnChanges(){
        this.initData();
    }

    initData(){
        //Playlist
        this._playlistService.getItemByID(this.playlistID).subscribe(
            (data : Playlist[] ) => {
                this.playlistInfo = data[0]
                
            }
        )

        //Videos
        this._videoService.getItemByPlaylistID(this.playlistID, this.totalItems).subscribe(
            (data: Video[] )=>{
                this.videos = data;
                this.subscriptionQuery = this._activatedRouteService.queryParams.subscribe(
                    (params: Params) => {
                        let currentPage : number = params['page'];
                        if(currentPage === undefined) currentPage = 1;
                        this.setPage(currentPage);
                        // console.log(this.pager);
                        
                    }
                );
            }
        )
    }

    setPage(page : number){
        // get pager object from service
        this.pager = this._pagerService.getPager(this.videos.length, +page, 5);

        if(this.pager){
            // get current page of items
            this.pagedItems = this.videos.slice(this.pager.startIndex, this.pager.endIndex + 1);
            
            this._routerService.navigate(['playlist', this.playlistID],{
                queryParams:{
                    page : page
                }
            })
        }else{
            this.pagedItems = this.videos;
        }
    }
    
    changeLayout(value : string){
        this.layout = value;
    }

    ngOnDestroy(){
        this.subscriptionQuery.unsubscribe();
    }

}



