import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { AppSetting } from '../../app.setting';
import { Playlist } from '../defines/playlist.class';

@Injectable()
export class PlaylistService {
    constructor(
        private _db: AngularFireDatabase
    ) { }

    getItems(): Observable<Playlist[]> {
        return this._db.list(AppSetting.TB_PLAYLIST, ref => ref.limitToFirst(4))
                        .valueChanges()
                        .map(Playlist.fromJsonList);
    };

    getItemByID(playlistID: string):Observable<any> {
        return this._db.list(AppSetting.TB_PLAYLIST, ref =>  ref.orderByChild('id').equalTo(playlistID))
                        .valueChanges()
                        .map(Playlist.fromJsonList);
    };

}


