export class Video {
    constructor(
        private _id             : string = '',
        private _publishedAt    : string = '',
        private _channelId      : string = '',
        private _playlistID     : string = '',
        private _title          : string = '',
        private _slug           : string = '',
        private _description    : string = '',
        private _thumbnails     : string = '',
        private _views          : number = 0,
        private _comments       : number = 0,
        private _ratting        : number = 0,
        private _featured       : boolean = false,
    ) { }

    public get id() {
        return this._id
    }
    public get publishedAt() {
        return this._publishedAt
    }
    public get channelId() {
        return this._channelId
    }
    public get playlistID() {
        return this._playlistID
    }
    public get title() {
        return this._title
    }
    public get slug() {
        return this._slug
    }
    public get description() {
        return this._description
    }
    public get thumbnails() {
        return this._thumbnails
    }
    public get views() {
        return this._views
    }
    public get comments() {
        return this._comments
    }
    public get ratting() {
        return this._ratting
    }
    public get featured() {
        return this._featured
    }

    static fromJson({ id, publishedAt, channelId, playlistID, title, slug, description, thumbnails, views, comments, ratting, featured }: Video) {
        return new Video(id, publishedAt, channelId, playlistID, title, slug, description, thumbnails, views, comments, ratting, featured)
    }

    static fromJsonList(array: any): Video[] {
        return array.map(Video.fromJson)
    }
}


