import { Component, OnInit, OnDestroy, AfterViewChecked } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Video } from './../../shared/defines/video.class';
import { VideoService } from '../../shared/services/video.service';
import { Playlist } from './../../shared/defines/playlist.class';
import { PlaylistService } from '../../shared/services/playlist.service';
import { Subscription } from 'rxjs/Subscription';


@Component({
    selector: 'zvideo-video',
    templateUrl: './../templates/video.component.html'
})

export class VideoComponent implements OnInit, OnDestroy, AfterViewChecked {
    videoID: string;
    videoInfo: Video = null;
    subscription : Subscription;

    constructor(
        private _activeRouteService: ActivatedRoute,
        private _videoService : VideoService,
        private _routeService : Router
    ) { }

    ngOnInit() {
        this.subscription = this._activeRouteService.params.subscribe(
            (params: Params) => {
                this.videoID = params.id
                //VideoInfo by videoID
                this._videoService.getItemByVideoID(this.videoID).subscribe(
                    data => this.videoInfo = data[0] 
                )
            }
        );
        
        
    }

    ngAfterViewChecked(){
        this._routeService.events.subscribe(
            event => window.scrollTo(0,0)
        )
    }

    ngOnDestroy(){
        this.subscription.unsubscribe();
    }


}



