import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { AppSetting } from '../../app.setting';
import { Video } from './../defines/video.class';

@Injectable()

export class VideoService {
    constructor(
        private _db: AngularFireDatabase
    ) { }

    getItemsFeatured(): Observable<Video[]> {
        return this._db.list(AppSetting.TB_VIDEO, ref => ref.orderByChild('featured').equalTo(true).limitToFirst(4))
                        .valueChanges()
                        .map(Video.fromJsonList);
    };

    getItemsPopular(): Observable<Video[]> {
        return this._db.list(AppSetting.TB_VIDEO, ref => ref.orderByChild('ratting').limitToLast(4))
                        .valueChanges()
                        .map(Video.fromJsonList);
    };

    getItemByPlaylistID(playlistID : string, totalItem : number = 4): Observable<Video[]> {
        return this._db.list(AppSetting.TB_VIDEO, ref => ref.orderByChild('playlistID').equalTo(playlistID).limitToFirst(totalItem))
                        .valueChanges()
                        .map(Video.fromJsonList);
    };

    getItemByVideoID(videoID: string): Observable<any> {
        return this._db.list(AppSetting.TB_VIDEO, ref => ref.orderByChild('id').equalTo(videoID).limitToFirst(1))
                        .valueChanges()
                        .map(Video.fromJsonList);
    };
}


