import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { PlaylistService } from '../../shared/services/playlist.service';
import { VideoService } from '../../shared/services/video.service';
import { Playlist } from './../../shared/defines/playlist.class';
import { Video } from './../../shared/defines/video.class';

@Component({
    selector: 'app-elm-playlist',
    templateUrl: './elm-playlist.component.html',
    styleUrls: ['./elm-playlist.component.css']
})
export class ElmPlaylistComponent implements OnInit, OnChanges {
    
    @Input('playlistID')    playlistID      : string;
    @Input('layout')        layout          : string = 'grid';
    @Input('totalItems')    totalItems      : number = 5;

    playlistInfo : Playlist = null;
    videos : Video[] = null;

    constructor(
        private _videoService : VideoService,
        private _playlistService : PlaylistService
    ) { }

    ngOnInit() {
        this.initData();
    }
    
    ngOnChanges(){
        this.initData();
    }

    initData(){
        //Playlist
        this._playlistService.getItemByID(this.playlistID).subscribe(
            (data : Playlist[] ) => {
                this.playlistInfo = data[0]
                // console.log(this.playlistInfo)
            }
        )

        //Videos
        this._videoService.getItemByPlaylistID(this.playlistID, this.totalItems).subscribe(
            (data: Video[] )=>{
                this.videos = data
                // console.log(data);
            }
        )
    }
    changeLayout(value : string){
        this.layout = value;
    }


}



